<template>
    <div class="newIndex">
        <div class="light">
            <div class="left common">
                <img src="./img/jt.png">
                <img src="./img/jt.png">
                <img src="./img/jt.png">
            </div>
            <div class="right common">
                <img src="./img/jt.png">
                <img src="./img/jt.png">
                <img src="./img/jt.png">
            </div>
        </div>
        <div class="wrap">
            <div class="text">很高兴又见面了，有什么可以帮您</div>
            <div class="box1 common" @click="jump('page6')">
                <img src="./img/1.png"/>
                <h2>定屏模式</h2>
            </div>
            <div class="box2 common" @click="toAI()">
                <img src="./img/2.png"/>
                <h2>AI模式</h2>
            </div>
            <div class="box3 common">
                <img src="./img/3.png"/>
                <h2>手动模式</h2>
            </div>
        </div>
    </div>
</template>
<script>

    // import yuying from './js/yuying'
    window.animationOK = false
    let speechInstance1 = ''
    let hasTold = false

    export default {
        name: "page",
        inject: ["app"],
        // mixins: [yuying],
        data() {
            return {
                toAIClick: false,
                count: 0
            }
        },
        methods: {
            toAI() {
                this.count++;
                if (this.count >= 3 && speechInstance1 && !hasTold) {
                    hasTold = true
                    speechInstance1 = new SpeechSynthesisUtterance('请长按空格键说话');
                    speechSynthesis.speak(speechInstance1);

                }
                if (!this.toAIClick) {
                    this.toAIClick = true
                    $('.box1,.box3').addClass('hide').find('img').removeClass('float')
                    $('.box2').addClass('haflHide').find('img').removeClass('float')
                    $('.light .common').addClass('light_act')
                    setTimeout(() => {
                        this.toAIClick = true
                        $('.text').animate({
                            'opacity': '1'
                        }, 1000, function () {
                            window.animationOK = true
                            speechInstance1 = new SpeechSynthesisUtterance('很高兴又见面了，有什么可以帮您');
                            speechSynthesis.speak(speechInstance1);
                        })
                    }, 1000)
                }

            },
            jump(target) {
                this.$router.push(target)
            }
        },
        created() {
        },
        mounted() {
            $('.wrap .common img').addClass('float')
        }
    }
</script>
